import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AppService } from '../services/app.service';

@Component({
  selector: 'app-single-notify',
  templateUrl: './single-notify.page.html',
  styleUrls: ['./single-notify.page.scss'],
})
export class SingleNotifyPage implements OnInit {

  id

  _url

  _removed_rn

  title
  img
  text
  _after
  date
  author

  data_loaded = false

  images_loading = true

  _trends

  article_images
  constructor(
    private _AppService:AppService, private _ActivatedRoute:ActivatedRoute,private _Router:Router

  ) { }
  recieve_data(){
    this._AppService.getPostDataForNotify(this.id).subscribe(data=>{
      console.log(`data from api : ${data['results']['title']}`);
     
      this.title = data['results']['title']
      this.img = data['results']['img']
      this.text =data['results']['text']
      this.date = data['results']['date']
      this.author = data['results']['author']
      // localStorage.clear();
      this._removed_rn = this.text.replace(/(?:\r\n|\r|\n)/g, '<br>');
      this._after = this._removed_rn.replace(/&quot;/g,' ')
    })
   
    // console.log(this.text)
    // console.log(_after)
  }

  back(){
    this._Router.navigate(['/home']);
    console.log('back')
  }

  related_news(){
    this.data_loaded = true
    this._AppService.get_data(`app_news_by_sections/58`,'r').subscribe((res:any)=>{
      console.log(res)
      this.data_loaded = true
      this._trends = res.results.section_58
    })
  }

  get_images(){
    this._AppService.get_data(`app_images_by_news_id/${this.id}`,'r').subscribe((res:any)=>{
      this.images_loading = false
      console.log(res)
      if(res != null){
        this.article_images = res.results
      }
    })
  }
  ngOnInit() {

    this.id = this._ActivatedRoute.snapshot.params['articleId'];
    this._url = `https://wa.me/?text=https://arriyadiyah.com/${this.id}`
     this.get_images()
    
    this.recieve_data()
     this.related_news()
  }

}
